import Vue from 'vue'
// vuex
// 1.  cnpm i vuex -S 
// 2. 
import Vuex from 'vuex'
// 3. vuexvue
Vue.use(Vuex)
// 4. new Vuex.Store()  
export default new Vuex.Store({
  state: {
    //  state   data ,
    //  store  this.$store.state.*** 
    count: 1,
    counter:2
  },
  mutations: {
    //   store  state   mutations  state  
    increment(state) {
      state.counter++
    },
    //   mutations  this.$store.commit('')
    //   mutations  this.$emit('')
    subtract(state, obj) {
      //  mutations  1  state  2  commit 
      console.log(obj)
      state.count -= (obj.c + obj.d)
    }
  },
  getters: {
    //  getters    state   mutations
    optCount: function (state) {
      return 'count' + state.count
    }
    //  getters    getters   
    //  getters  computed   state  getters   getters 
  }
})

// 
// 1. state mutations
// 2.   state   this.$store.state.***
// 3.   mutations  this.$store.commit('' )
// 4.  store  state    getters,  getters , this.$store.getters.***



